<template>
  <transition name="modal">
    <div v-if="show" class="modal-overlay" @click.self="closeModal">
      <div class="modal-container">
        <button class="close-btn" @click="closeModal">√ó</button>
        <h1>Î≥ºÎçîÎßÅ ÌïòÎäî Î≤ï</h1>
        <p class="subtitle">Í∏∞Ï¥àÎ∂ÄÌÑ∞ Ï∞®Í∑ºÏ∞®Í∑º ÏïåÏïÑÎ¥êÏöî</p>

        <div class="progress-bar">
          <div class="progress-fill" :style="{ width: progressPercent + '%' }"></div>
        </div>

        <div class="content-screen">
            <div :key="currentSlide" class="slide">
              <div class="media-container">
                <button
                  class="nav-btn nav-btn-left"
                  :disabled="currentSlide === 0"
                  @click="prevSlide"
                  aria-label="Ïù¥Ï†Ñ"
                >
                  ‚Äπ
                </button>
                <img
                  :src="publicPath + 'images/' + slides[currentSlide].image"
                  :alt="'Î≥ºÎçîÎßÅ ÌïòÎäî Î≤ï ' + (currentSlide + 1)"
                  class="slide-image"
                />
                <button
                  class="nav-btn nav-btn-right"
                  :disabled="currentSlide === slides.length - 1"
                  @click="nextSlide"
                  aria-label="Îã§Ïùå"
                >
                  ‚Ä∫
                </button>
              </div>

              <div class="slide-description" v-html="slides[currentSlide].description"></div>
            </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue'

export default {
  name: 'HowToClimbModal',
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  emits: ['close'],
  setup(props, { emit }) {
    const currentSlide = ref(0)
    const publicPath = process.env.BASE_URL

    const slides = [
      {
        description: '"ÌôÄÎìú"Îäî Ïö∞Î¶¨Í∞Ä Ïû°Í≥† Î∞üÎäî ÎèåÏùÑ ÌÜµÏπ≠Ìï©ÎãàÎã§.<br>"Î≥ºÎ•®"ÏùÄ Í∑∏Ï§ëÏóêÏÑúÎèÑ Í±∞ÎåÄÌïú Î∂ÄÌîºÏùò ÌôÄÎìúÎ•º ÏùòÎØ∏Ìï©ÎãàÎã§.<br>Ïù¥ ÌôÄÎìúÎì§ÏùÑ ÌÉÄÍ≥† Ïò¨ÎùºÍ∞Ä TOPÏóê ÎèÑÎã¨Ìï¥Î¥ÖÏãúÎã§!',
        image: 'how-to-1.jpg'
      },
      {
        description: 'Í∞ÄÏû• Î®ºÏ†Ä ÎÇ¥Í∞Ä ÌíÄ Î¨∏Ï†úÏùò STARTÏôÄ TOPÏùÑ ÌôïÏù∏Ìï©ÎãàÎã§.<br>ÎÇúÏù¥ÎèÑÎäî ÌÖåÏù¥ÌîÑ ÏÉâÏÉÅÏù¥Î©∞ ÌôÄÎìú ÏÉâÏÉÅÍ≥ºÎäî Î≥ÑÍ∞úÏûÖÎãàÎã§.<br>ÌöåÏÉâ ÎÇúÏù¥ÎèÑÏùò ÎÖ∏ÎûÄÌôÄÎìú, ÌååÎûÄÏÉâ ÎÇúÏù¥ÎèÑÏùò ÌååÎûÄÌôÄÎìú Ïù¥Îü∞ ÏãùÏûÖÎãàÎã§.',
        image: 'how-to-2.jpg'
      },
      {
        description: 'ÌöåÏÉâ ÎÇúÏù¥ÎèÑÏùò ÎÖ∏ÎûÄÌôÄÎìú Î¨∏Ï†úÎ•º Î¥ÖÏãúÎã§. Ïö∞ÏÑ† Î£®Ìä∏Î•º ÌååÏïÖÌï©ÎãàÎã§.<br>Ïñ¥ÎñªÍ≤å Ïò¨ÎùºÍ∞ÄÏïºÌï†ÏßÄ Ï∞æÏïÑÎ≥¥Îäî Í≤ÉÏùÑ "Î£®Ìä∏ÌååÏù∏Îî©" Ïù¥ÎùºÍ≥† Ìï©ÎãàÎã§.<br>Î¨∏Ï†úÎ•º ÌíÄÍ∏∞ Ï†Ñ Îã§Î•∏ÏÇ¨ÎûåÍ≥º Î£®Ìä∏Í∞Ä Í≤πÏπòÎäî ÏßÄ Íº≠ ÌôïÏù∏Ìï¥ÏïºÌï©ÎãàÎã§.',
        image: 'how-to-3.jpg'
      },
      {
        description: 'Ïûê Ïù¥Ï†úÎ∂ÄÌÑ∞Îäî ÎÇ¥ Î¨∏Ï†úÏóê Ìï¥ÎãπÌïòÎäî ÏÉâÏÉÅÏùò ÌôÄÎìúÎßå ÏÇ¨Ïö©Ìï©ÎãàÎã§.<br>Îì±Î∞ò Ï§ë Îã§Î•∏ ÌôÄÎìúÎ•º Ïû°Í±∞ÎÇò Î∞üÎäî Í≤ÉÏùÑ "Îåë"Ïù¥ÎùºÍ≥† ÌïòÎäîÎç∞,<br>Ïù¥ Í≤ΩÏö∞ Î¨∏Ï†úÎ•º ÌíÄÏóàÎã§Í≥† Ïù∏Ï†ïÎêòÏßÄ ÏïäÏäµÎãàÎã§. Ïã§ÏàòÎ°ú ÌïòÎ©¥ ÏñµÏö∏...<br>',
        image: 'how-to-4.jpg'
      },
      {
        description: 'Îß§Ìä∏ÏóêÏÑú Îëê Î∞úÏù¥ Îã§ Îñ®Ïñ¥ÏßÑ ÏÉÅÌÉúÏóêÏÑú Î≤ÑÌÖ®Ïïº START ÏûÖÎãàÎã§.<br>Î∞úÏùÑ ÎñºÏûêÎßàÏûê Î∞îÎ°ú Îã§Ïùå ÌôÄÎìúÎ•º Ïû°ÏúºÎ©¥ Ïïà Îê©ÎãàÎã§.<br>Î∞úÏùÑ Î≤ΩÏóê ÎåÄÍ±∞ÎÇò Í≥µÏ§ëÏóê ÎùÑÏõåÎèÑ Îê©ÎãàÎã§.',
        image: 'how-to-5.jpg'
      },
      {
        description: 'Î£®Ìä∏ÌååÏù∏Îî©ÏùÑ Îñ†Ïò¨Î¶¨Î©∞ Ïó¥Ïã¨Ìûà Îì±Î∞òÌï©ÎãàÎã§.<br>Ï∞∏Í≥†Î°ú Î≥ºÎ•®Ïóê Ìï¥Îãπ ÏÉâÏÉÅÏùò ÌôÄÎìúÍ∞Ä Î∞ïÌòÄÏûàÎã§Î©¥<br>Í∑∏ Î≥ºÎ•® Ï†ÑÏ≤¥Î•º ÏÇ¨Ïö©Ìï† Ïàò ÏûàÏäµÎãàÎã§. Ïûò ÌôúÏö©Ìï©ÏãúÎã§.',
        image: 'how-to-6.jpg'
      },
      {
        description: 'TOP ÌôÄÎìúÎ•º ÏñëÏÜêÏúºÎ°ú Ïû°Í≥† 3Ï¥àÍ∞Ñ Ïú†ÏßÄÌïòÎ©¥ ÏôÑÎì±ÏûÖÎãàÎã§!<br>START ÎïåÏôÄ ÎßàÏ∞¨Í∞ÄÏßÄÎ°ú ÏôÑÏ†ÑÌûà Ï†úÏïïÌï¥ÏÑú Î≤ÑÌÖ®ÏïºÌï©ÎãàÎã§.<br>ÏôÑÎì± ÌõÑ Î∞îÎ°ú Îß§Ìä∏Î°ú Îõ∞Ïñ¥ÎÇ¥Î¶¨Î©¥ ÏúÑÌóòÌï©ÎãàÎã§.',
        image: 'how-to-7.jpg'
      },
      {
        description: '"Îã§Ïö¥ÌÅ¥ÎùºÏù¥Î∞ç" ÎïåÎäî ÏïÑÎ¨¥ ÌôÄÎìúÎ•º Ïû°Í≥† ÎÇ¥Î†§ÏôÄÎèÑÎê©ÎãàÎã§.<br>Ï§ëÍ∞ÑÏ§ëÍ∞Ñ Ïû°Í∏∞ Ï¢ãÏùÄ "Îã§Ïö¥ÌôÄÎìú"Îì§ÏùÑ Î∞∞ÏπòÌï¥ÎëêÎØÄÎ°ú ÏÇ¨Ïö©ÌïòÎ©¥ Ï¢ãÏäµÎãàÎã§.<br>Îß§Ìä∏ Í∞ÄÍπåÏù¥ÍπåÏßÄ ÎÇ¥Î†§ÏôÄÏÑú ÏñëÎ∞úÎ°ú Ï∞©ÏßÄÌï¥Ï£ºÏÑ∏Ïöî.',
        image: 'how-to-8.jpg'
      }
    ]

    const progressPercent = computed(() => {
      return ((currentSlide.value + 1) / slides.length) * 100
    })

    const closeModal = () => {
      currentSlide.value = 0
      emit('close')
    }

    const nextSlide = () => {
      if (currentSlide.value < slides.length - 1) {
        currentSlide.value++
      }
    }

    const prevSlide = () => {
      if (currentSlide.value > 0) {
        currentSlide.value--
      }
    }

    const handleKeydown = (e) => {
      if (!props.show) return

      if (e.key === 'Escape') closeModal()
      if (e.key === 'ArrowRight') nextSlide()
      if (e.key === 'ArrowLeft') prevSlide()
    }

    onMounted(() => {
      document.addEventListener('keydown', handleKeydown)
    })

    onUnmounted(() => {
      document.removeEventListener('keydown', handleKeydown)
    })

    return {
      currentSlide,
      slides,
      progressPercent,
      publicPath,
      closeModal,
      nextSlide,
      prevSlide
    }
  }
}
</script>

