<template>
  <transition name="modal">
    <div v-if="show" class="modal-overlay" @click.self="closeModal">
      <div class="modal-container">
        <button class="close-btn" @click="closeModal">√ó</button>
        <h1>Î≥ºÎçîÎßÅ Ï£ºÏùòÏÇ¨Ìï≠</h1>
        <p class="subtitle">ÏïàÏ†ÑÌïú ÌÅ¥ÎùºÏù¥Î∞çÏùÑ ÏúÑÌïú ÌïÑÏàò Í∞ÄÏù¥Îìú</p>

        <div class="progress-bar">
          <div class="progress-fill" :style="{ width: progressPercent + '%' }"></div>
        </div>

        <div class="video-screen">
          <transition name="fade" mode="out-in">
            <div :key="currentScene" class="scene">
              <div class="scene-icon">{{ scenes[currentScene].icon }}</div>
              <div class="scene-title">{{ scenes[currentScene].title }}</div>
              <div class="scene-description" v-html="scenes[currentScene].description"></div>
            </div>
          </transition>
        </div>

        <div class="controls">
          <button class="btn-secondary" :disabled="currentScene === 0" @click="prevScene">‚Üê Ïù¥Ï†Ñ</button>
          <button class="btn-secondary" :disabled="currentScene === scenes.length - 1" @click="nextScene">Îã§Ïùå ‚Üí</button>
        </div>

        <div class="warning-box">
          <span class="icon">‚ö†Ô∏è</span>
          <span class="text">Î∂ÄÏÉÅ ÏòàÎ∞©ÏùÑ ÏúÑÌï¥ Ï£ºÏùòÏÇ¨Ìï≠ÏùÑ Íº≠ ÏßÄÏºúÏ£ºÏÑ∏Ïöî!</span>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue'

export default {
  name: 'CautionModal',
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  emits: ['close'],
  setup(props, { emit }) {
    const currentScene = ref(0)

    const scenes = [
      {
        icon: '‚≠êÔ∏è',
        title: 'Î≥ºÎçîÎßÅ Ï£ºÏùòÏÇ¨Ìï≠',
        description: 'ÏïàÏ†ÑÌïòÍ≥† Ï¶êÍ±∞Ïö¥ ÌÅ¥ÎùºÏù¥Î∞çÏùÑ ÏúÑÌï¥<br>Íº≠ ÏïåÏïÑÏïº Ìï† Ï£ºÏùòÏÇ¨Ìï≠ÏùÑ ÏïåÎ†§ÎìúÎ¶¥Í≤åÏöî!'
      },
      {
        icon: 'ü§∏',
        title: '1. Ï∂©Î∂ÑÌïú Ï§ÄÎπÑÏö¥Îèô',
        description: 'ÌÅ¥ÎùºÏù¥Î∞ç Ï†Ñ ÏµúÏÜå 10Î∂Ñ Ïù¥ÏÉÅ<br>Ïä§Ìä∏Î†àÏπ≠Í≥º Ï§ÄÎπÑÏö¥ÎèôÏùÑ Ìï¥Ï£ºÏÑ∏Ïöî.<br>Ïñ¥Íπ®, ÌóàÎ¶¨Î•º Ï∂©Î∂ÑÌûà ÌíÄÏñ¥Ï£ºÎäî Í≤ÉÏù¥ Ï§ëÏöîÌï©ÎãàÎã§.'
      },
      {
        icon: 'ü™Ç',
        title: '2. Ïò¨Î∞îÎ•∏ ÎÇôÌïò ÏûêÏÑ∏',
        description: 'ÎÇôÌïò Ïãú Î∞úÎ∂ÄÌÑ∞ Ï∞©ÏßÄÌïòÍ≥†<br>Î¨¥Î¶éÏùÑ ÏÇ¥Ïßù Íµ¨Î∂ÄÎ†§ Ï∂©Í≤©ÏùÑ Ìù°ÏàòÌïòÏÑ∏Ïöî.<br>Ï†àÎåÄ ÏÜêÏúºÎ°ú Î∞îÎã•ÏùÑ ÏßöÏßÄ ÎßàÏÑ∏Ïöî!'
      },
      {
        icon: 'üí™',
        title: '3. Ï≤¥Î†• Í¥ÄÎ¶¨',
        description: 'Î¨¥Î¶¨ÌïòÏßÄ ÎßêÍ≥† ÏûêÏã†Ïùò ÏàòÏ§ÄÏóê ÎßûÍ≤å<br>Ï≤úÏ≤úÌûà ÎÇúÏù¥ÎèÑÎ•º ÎÜíÏó¨Í∞ÄÏÑ∏Ïöî.<br>Ï∂©Î∂ÑÌïú Ìú¥ÏãùÎèÑ Ï§ëÏöîÌï©ÎãàÎã§.'
      },
      {
        icon: '‚¨úÔ∏è',
        title: '4. Îß§Ìä∏',
        description: 'Îß§Ìä∏ ÏúÑÏóê Î¨ºÍ±¥ÏùÑ Ïò¨Î†§ÎëêÍ±∞ÎÇò<br>ÏïâÏïÑÏûàÏúºÎ©¥ ÏúÑÌóòÌï©ÎãàÎã§.<br>Îß§Ìä∏ ÏúÑÏóêÎäî Ìïú ÏÇ¨ÎûåÎßå ÏûàÎèÑÎ°ù Ìï¥Ïöî.'
      },
      {
        icon: 'üëü',
        title: '5. ÏïîÎ≤ΩÌôî',
        description: 'Îì±Î∞òÏùÑ Ìï† ÎïåÎäî Íº≠ ÏïîÎ≤ΩÌôîÎ•º<br>Ïã†Í≥† Îì±Î∞òÌï¥ÏïºÌï©ÎãàÎã§.<br>Îß®Î∞úÎ°ú Îì±Î∞òÌïòÏßÄ ÎßêÏïÑÏöî.'
      },
      {
        icon: 'ü§ù',
        title: '6. ÏïîÏû• ÏóêÌã∞Ïºì',
        description: 'Î¨∏Ï†úÎ•º ÌíÄÍ∏∞ Ï†Ñ Î£®Ìä∏Í∞Ä Í≤πÏπòÎäî ÏßÄ ÌôïÏù∏ÌïòÍ≥†<br>ÎåÄÍ∏∞ ÏàúÏÑúÎ•º ÏßÄÏºúÏ£ºÏÑ∏Ïöî.<br>ÏÑúÎ°ú ÏùëÏõêÌïòÍ≥† Î∞∞Î†§ÌïòÎäî Î¨∏ÌôîÎ•º ÎßåÎì§Ïñ¥Ïöî!'
      },
      {
        icon: '‚ú®',
        title: 'ÏïàÏ†ÑÌïú ÌÅ¥ÎùºÏù¥Î∞ç!',
        description: 'Ï£ºÏùòÏÇ¨Ìï≠ÏùÑ Ïûò ÏßÄÏºúÏÑú<br>Ï¶êÍ≤ÅÍ≥† ÏïàÏ†ÑÌïú Î≥ºÎçîÎßÅ ÌïòÏÑ∏Ïöî!'
      }
    ]

    const progressPercent = computed(() => {
      return ((currentScene.value + 1) / scenes.length) * 100
    })

    const closeModal = () => {
      currentScene.value = 0
      emit('close')
    }

    const nextScene = () => {
      if (currentScene.value < scenes.length - 1) {
        currentScene.value++
      }
    }

    const prevScene = () => {
      if (currentScene.value > 0) {
        currentScene.value--
      }
    }

    const handleKeydown = (e) => {
      if (!props.show) return
      
      if (e.key === 'Escape') closeModal()
      if (e.key === 'ArrowRight') nextScene()
      if (e.key === 'ArrowLeft') prevScene()
    }

    onMounted(() => {
      document.addEventListener('keydown', handleKeydown)
    })

    onUnmounted(() => {
      document.removeEventListener('keydown', handleKeydown)
    })

    return {
      currentScene,
      scenes,
      progressPercent,
      closeModal,
      nextScene,
      prevScene
    }
  }
}
</script>

